.content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 99.5vh;
  width: 70vw;

}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#bongo-cat {

  height: 40vh;
  width: 40vw;



}

#paw-right--up,
#paw-right--down,
#paw-left--up,
#paw-left--down {
  animation: blink 300ms infinite;
}

#paw-right--up,
#paw-left--down {
  animation-delay: 150ms;
}

@keyframes blink {
  0% {
    opacity: 0;
  }

  49% {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }
}

#laptop__code {
  transform: rotateX(-37deg) rotateY(-46deg) rotateZ(-23deg) translateX(8px) translateY(20px) translateZ(-50px);
}

.typewriter h1 {
  color: black;
  font-family: monospace;
  font-size: xxx-large;
  overflow: hidden;
  /* Ensures the content is not revealed until the animation */
  border-right: .15em solid orange;
  /* The typwriter cursor */
  white-space: nowrap;
  /* Keeps the content on a single line */
  margin: 0 auto;
  /* Gives that scrolling effect as the typing happens */
  letter-spacing: .15em;
  /* Adjust as needed */
  animation:
    typing 3.5s steps(30, end),
    blink-caret .5s step-end infinite;
}

/* The typing effect */
@keyframes typing {
  from {
    width: 0
  }

  to {
    width: 100%
  }
}

/* The typewriter cursor effect */
@keyframes blink-caret {

  from,
  to {
    border-color: transparent
  }

  50% {
    border-color: orange
  }
}

.typewriter .highlight {
  color: orange;
  /* Add additional styles for highlighting if desired */
}

.typewriter {
  padding-bottom: 50px;
}

@keyframes backspacing {
  from {
    width: 100%;
  }

  to {
    width: 0;
  }
}
